

























import 'reflect-metadata';
import { Component, Prop, Watch, Mixins } from 'vue-property-decorator';
import ValidatedInputEl from '@/mixins/ValidatedInputEl';
import CombineErrors from '@/mixins/CombineErrors';
// import { IValCtxt } from '@/mixins/StateWatcher';

@Component({})
export default class ValidatedInputSelectGroup extends Mixins(ValidatedInputEl, CombineErrors) {
  @Prop({ required: true })
  selectValue!: any;

  @Prop({ default: 'required' })
  selectRules?: any;

  @Prop({ default: void 0 })
  selectTitle?: string;

  @Prop({ default: void 0 })
  selectName?: string;

  @Prop({ default: void 0 })
  selectErrorLabel?: string;

  pSelectValue: any = '';
  pSelectName = '';
  pSelectErrorLabel = '';

  beforeMount() {
    this.pSelectName = this.selectName || (this.pName + '-select-append');
    this.pSelectErrorLabel = this.selectErrorLabel || this.pSelectLabel;
  }

  get pSelectLabel() {
    return this.selectTitle || `[${this.label || this.pName}] UNITS`;
  }

  @Watch('selectValue', { immediate: true })
  syncSelect(newVal: any) {
    this.pSelectValue = newVal;
  }

  @Watch('pSelectValue')
  syncPSelect(newVal: any) {
    if (newVal === this.selectValue) { return; }
    this.$emit('update:select-value', newVal);
  }
}

